
body {
  background: -webkit-gradient(linear, left top, left bottom, from(#dfdfec), to(#ecdfe8)) no-repeat center center fixed;
  background: linear-gradient(180deg, #dfdfec, #ecdfe8) no-repeat center center fixed;
  font-family: sans-serif;
}
.chart-area {
  position: absolute;
  -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
}
.flag-hack {
  position: absolute;
  top: 0px;
}
.flag {
  position: absolute;
}
.graph {
  background-color: #fff;
}
.header {
  margin: 0px;
  text-align: center;
  background-color: #fff;
  height: 100px;
}
.header h1 {
  background-color: #88f;
  color: #fff;
  margin: 0px;
  padding: 15px 0 15px 0;
  -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
          box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
}



